
.body{
   
    text-align: center;
        color:#fff;
        font-family: 'Jost', sans-serif;
        background-image: url(../assets/shapei.png);
        overflow-y: scroll;
    
    height:100%;
    }
.stake{
    font-size:47px;
    font-weight:600;
    font-family:'Jost', sans-serif;
    margin-top:20px;
    margin-bottom: 20px;

}
.right{
    border-radius: 0 45px 45px 0;
    height:46px;
    padding-right: 25px;
    padding-left:25px;
    color:white;
    border:1px solid #2b3456;

}
.left{
    border-radius:  45px 0 0 45px;
    height:46px;
    padding-right: 25px;
    padding-left:25px;
    color:white;
    border:1px solid #2b3456;
}
.active{
   background: #2b3456;;
}
.inactive{
     background: transparent;
}
.toggle{
    margin-top:20px;
    font-size:21px;
    font-weight:600;
    font-family:'Jost', sans-serif;;
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
}
