
  
    .boxTime{
        width:70%;
        background-color: rgb(36, 43, 67);
        background-image: url(../assets/bg-star.svg);
        border-radius:20px;
        margin:auto;
        padding:25px;
    }
    .farming{
        font-size:33px;
        font-weight:700;
    
    }
    .circle {
        height: 145px;
        width: 145px;
        line-height: 127px;
        border-radius: 95px;
        border: 8px solid #a0c6ff;
        color: #fff;
        font-size: 21px;
        font-weight: 600;
        margin: auto;
        margin-top: 27px;
        margin-bottom: 27px;
    }
    
    
    .row{
        display:flex;
        flex-wrap: wrap;
        justify-content: center;
        
    }
    .btn{
        width:188px;
        height:49px;
        border-radius: 7px;
        border:none;
        background-image: linear-gradient(to left,#5c9eff 0,#60a0ff 100%);
        color:#fff;
        font-size:18px ;
        font-weight: 700;
        font-family:'Jost', sans-serif; ;
    }
    
  
    
