
.menue{
background-color: rgb(36, 43, 67);
overflow-y: scroll;
overflow-x: visible;

height:100%;
}

  
.menueItem{
display:flex;
align-items:center;
font-family:'Jost', sans-serif;;
font-weight:500;
font-size:18px;
color:#ccd7ff;
height:49px;
margin-top:8px;
justify-content: space-between;
cursor: pointer;
}
.menueItem:hover{
    background-color: #1c2238;
}
.c11{
    display:inline-block;
}
.c22{
    display:inline-block;
    color:white;
}
.c1{
    display:inline-block;
    margin-left:10px;

}
.c2{
    display:inline-block;
    margin-left:10px;

}
.icon{
    width:25px;
    height:25px;
}

.menue::-webkit-scrollbar {
    width: 3px;
  }
   
  .menue::-webkit-scrollbar-track {
    box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  }
   
  .menue::-webkit-scrollbar-thumb {
    background-color: #31d0aa;
    outline: 1px solid slategrey;
  }
.subMenue{
    color:#ccd7ff;
    
    font-family:'Jost', sans-serif;;
    font-weight:500;
    font-size:14px;
   

}
.subMenueItem{
    padding: 10px 10px;
    padding-left:35px;
    margin:auto;
    cursor: pointer;
}
.subMenueItem:hover{
    background-color: #2b4782;
}
.arrow{
    padding-left:80px;
    padding-right: 10px;
}
.hide{
    display:none;
    }
.below{
    position: fixed;
    bottom: 0px;
    color: #fff;
    
    background: #2c3248;
    font-family: 'Jost', sans-serif;
    font-size: 14px;
    font-weight: 500;
    width:20%; 
}
.social{
    padding: 2px 16px;
    padding-top: 13px;
    display: flex;
    justify-content: space-between;
    width:100%;
}
.rugdoc{
    width: 100%;
    height:120px;
}

.mainLogo{
    width:17px;
    Height:17px;
}
a{color:white}

@media screen and (max-width: 390px) {
    .c2_s{
display: none;
    }
}
.ppp{
    overflow:hidden;
}
.boosted{
    width:100px;
    height: 35px;
    margin-left: 10px;
}